import React, { Component, Fragment } from 'react';
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom';
import Login from './components/login/Login';
import Dashboard from './components/dashboard/Dashboard';
import AddRestaurant from './components/addRestaurant/AddRestaurant';
import RestaurantList from './components/restaurantList/RestaurantList';
import AddIcons from './components/addIcons/AddIcons';
import Header from './components/header/Header';
import Sidebar from './components/sidebar/Sidebar';
import Error404 from './components/error/Error';
import endpoint from './helpers/Endpoint';
import Notification from 'react-bulma-notification';
import 'bulma/css/bulma.css';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loggedIn: false,
      style: '',
      oPassword: '',
      nPassword: ''
    };
  }

  openNotification = (type, msg) => {
    Notification[type](<strong>{msg}</strong>, { duration: 5 });
  };

  componentDidMount = () => {
    const loggedIn = localStorage.getItem('loggedIn');
    this.setState({ loggedIn });
  };

  login = (e, email, password) => {
    e.preventDefault();
    const data = JSON.stringify({ email, password });
    function handleErrors(result) {
      if (!result.ok) {
        throw Error(result.status);
      }
      return result;
    }
    fetch(`${endpoint}/admin/login`, {
      method: 'POST',
      body: data,
      headers: {
        'Content-Type': 'application/json'
      }
    })
      //.then(handleErrors)
      .then(result => result.json())
      .then(result => {
        if (result.status === 'failure') {
          console.log('errors');
          this.openNotification('error', result.data);
        } else {
          const token = result.data.token;
          localStorage.setItem('token', token);
          localStorage.setItem('loggedIn', true);
          this.setState({ loggedIn: true });
          this.props.history.push('/');
          this.openNotification('success', result.message);
        }
      })
      .catch(error => console.log(error));
  };

  logOut = e => {
    e.preventDefault();
    const token = localStorage.getItem('token');
    function handleErrors(result) {
      if (!result.ok) {
        throw Error(result.status);
      }
      return result;
    }
    fetch(`${endpoint}/admin/logout`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'x-auth-token': token
      }
    })
      .then(handleErrors)
      .then(result => result.json())
      .then(result => {
        if (result.status === 'failure') {
          console.log('errors');
          localStorage.clear();
          this.setState({ loggedIn: false });
        } else {
          localStorage.clear();
          this.setState({ loggedIn: false });
        }
      })
      .catch(error => console.log(error));
  };

  changePasswordDialogue = () => {
    this.setState({ style: 'is-active' });
  };

  closeModal = () => {
    this.setState({ style: '' });
  };

  changePassword = e => {
    e.preventDefault();
    const { oPassword, nPassword } = this.state;
    const data = JSON.stringify({ oPassword, nPassword });
    const token = localStorage.getItem('token');
    function handleErrors(result) {
      if (!result.ok) {
        throw Error(result.status);
      }
      return result;
    }
    fetch(`${endpoint}/admin/change-password`, {
      method: 'POST',
      body: data,
      headers: {
        'Content-Type': 'application/json',
        'x-auth-token': token
      }
    })
      .then(handleErrors)
      .then(result => result.json())
      .then(result => {
        if (result.status === 'failure') {
          console.log(result.message);
          this.openNotification('error', result.message);
        } else {
          console.log(result);
          this.openNotification('success', result.message);
          this.setState({ style: '', oPassword: '', nPassword: '' });
        }
      })
      .catch(error => console.log(error));
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    const { loggedIn, style, oPassword, nPassword } = this.state;
    return (
      <Fragment>
        {loggedIn ? (
          <Dashboard
            logOut={this.logOut}
            changePassword={this.changePasswordDialogue}
          />
        ) : (
          <Fragment>
            <Header loggedIn={false} />
            <Login
              login={(e, email, password) => this.login(e, email, password)}
            />
          </Fragment>
        )}
        <div className="container is-widescreen">
          <div className="columns is-centered is-3">
            <BrowserRouter>
              <Fragment>
                <div className="column is-one-quarter">
                  {loggedIn ? <Sidebar /> : ''}
                </div>
                <div className="column is-three-quarters">
                  <Switch>
                    <ProtectedRoute exact path="/" component={RestaurantList} />
                    <ProtectedRoute
                      path="/add-restaurant"
                      component={AddRestaurant}
                    />
                    <ProtectedRoute path="/add-icons" component={AddIcons} />
                    <ProtectedRoute component={Error404} />
                  </Switch>
                </div>
              </Fragment>
            </BrowserRouter>
          </div>
        </div>
        <div className={`modal ${style}`}>
          <div className="modal-background" />
          <div className="modal-content" style={{ zIndex: 1 }}>
            <div className="tile is-parent">
              <article className="tile is-child notification ">
                <div className="content">
                  <div className="file">
                    {/* <label className="file-label">
                      <input
                        style={{ color: '#333' }}
                        className="file-input"
                        type="file"
                        name="icon"
                        onChange={this.handleIconChange}
                      />
                      <span className="file-cta">
                        <span className="file-icon">
                          <i className="fas fa-upload" />
                        </span>
                        <span className="file-label">Choose a file…</span>
                      </span>
                    </label> */}
                    <form onSubmit={this.changePassword}>
                      <div className="field">
                        <label className="label">Current Password</label>
                        <div className="control">
                          <input
                            className="input"
                            type="password"
                            name="oPassword"
                            placeholder="Current Password"
                            value={oPassword}
                            onChange={this.handleChange}
                            required
                          />
                        </div>
                      </div>
                      <div className="field">
                        <label className="label">New Password</label>
                        <div className="control">
                          <input
                            className="input"
                            type="password"
                            name="nPassword"
                            placeholder="New Password"
                            value={nPassword}
                            onChange={this.handleChange}
                            required
                          />
                        </div>
                      </div>
                      <div className="control">
                        <button className="button is-primary">Submit</button>
                      </div>
                    </form>
                  </div>
                </div>
              </article>
            </div>
          </div>
          <div
            onClick={this.closeModal}
            style={{
              minWidth: '100%',
              minHeight: '100%',
              position: 'absolute'
            }}
          >
            <button className="modal-close is-large" aria-label="close" />
          </div>
        </div>
      </Fragment>
    );
  }
}

class ProtectedRoute extends Component {
  render() {
    let authenticated;
    if (localStorage.getItem('loggedIn') === 'true') {
      authenticated = true;
    } else {
      authenticated = false;
    }
    const { component: Component, ...props } = this.props;

    return (
      <Route
        {...props}
        render={props =>
          authenticated ? <Component {...props} /> : <Redirect to="/" />
        }
      />
    );
  }
}

export default App;
