import React, { Component, Fragment } from 'react';
import Restaurant from '../restaurant/Restaurant';
import endpoint from '../../helpers/Endpoint';

class RestaurantList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      restaurants: [],
      restaurant: [],
      isLoaded: false,
      currentPage: 'restaurantList'
    };
  }

  componentDidMount = () => {
    const token = localStorage.getItem('token');
    function handleErrors(result) {
      if (!result.ok) {
        throw Error(result.status);
      }
      return result;
    }
    fetch(`${endpoint}/admin/restaurant`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'x-auth-token': token
      }
    })
      .then(handleErrors)
      .then(result => result.json())
      .then(result => {
        if (result.status === 'failure') {
          console.log(result);
        } else {
          console.log(result.data);
          this.setState({ restaurants: result.data, isLoaded: true });
        }
      })
      .catch(error => console.log(error));
  };

  showRestaurant = restaurant => {
    this.setState({ currentPage: 'singleRestaurant', restaurant });
  };

  render() {
    console.log(this.state);
    const { restaurants, isLoaded, restaurant, currentPage } = this.state;
    let authenticated;
    if (localStorage.getItem('loggedIn') === 'true') {
      authenticated = true;
    } else {
      authenticated = false;
    }

    if (!authenticated) {
      return <Fragment />;
    }

    if (!isLoaded) {
      return <Fragment>Loading...</Fragment>;
    } else if (currentPage === 'restaurantList') {
      return (
        <Fragment>
          <div className="columns is-centered">
            <div className="column">
              <table className="table is-fullwidth">
                <thead>
                  <tr>
                    <th>
                      <abbr title="Serial Number">S.No.</abbr>
                    </th>
                    <th>Name</th>
                    <th>Email</th>
                    <th>Phone No.</th>
                    <th>Address</th>
                    <th>Icon</th>
                  </tr>
                </thead>
                <tbody>
                  {restaurants.map((item, index) => {
                    return (
                      <tr key={item.phoneNo}>
                        <td>{index + 1}</td>
                        <td>
                          <a onClick={() => this.showRestaurant(item)}>
                            {item.name}
                          </a>
                        </td>
                        <td>{item.email}</td>
                        <td>{item.phoneNo}</td>
                        <td>
                          {item.address.line1}, {item.address.line2},{' '}
                          {item.address.city}, {item.address.pin},{' '}
                          {item.address.state}, {item.address.country}
                        </td>
                        <td>
                          <img
                            src={item.logo ? `${endpoint}/${item.logo}` : ''}
                            style={{ maxWidth: '100px', height: 'auto' }}
                          />
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </Fragment>
      );
    } else if (currentPage == 'singleRestaurant') {
      return (
        <Fragment>
          <Restaurant restaurant={restaurant} />
        </Fragment>
      );
    } else {
      return <div>error!!</div>;
    }
  }
}

export default RestaurantList;
