import React, { Component } from 'react';

class RestaurantForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      restaurant: [],
      name: '',
      email: '',
      phoneNo: '',
      taxNo: '',
      line1: '',
      line2: '',
      city: '',
      pin: '',
      state: '',
      country: '',
      description: '',
      managerEmail: '',
      style: '',
      id: '',
      accountNumber: '',
      website: ''
    };
  }

  componentDidMount = () => {
    const { restaurant, editing } = this.props;
    if (editing) {
      this.setState({
        name: restaurant.name,
        email: restaurant.email,
        phoneNo: restaurant.phoneNo,
        taxNo: restaurant.taxNo,
        line1: restaurant.address.line1,
        line2: restaurant.address.line2,
        city: restaurant.address.city,
        pin: restaurant.address.pin,
        state: restaurant.address.state,
        country: restaurant.address.country,
        description: restaurant.description,
        accountNumber: restaurant.accountNumber,
        id: restaurant._id,
        website: restaurant.website
      });
    }
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    console.log(this.props);
    const { editing } = this.props;
    const {
      name,
      email,
      phoneNo,
      taxNo,
      line1,
      line2,
      city,
      state,
      pin,
      country,
      description,
      managerEmail,
      accountNumber,
      id,
      website
    } = this.state;
    return (
      <form
        onSubmit={e =>
          this.props.saveRestaurant(
            e,
            editing,
            name,
            email,
            phoneNo,
            taxNo,
            line1,
            line2,
            city,
            pin,
            state,
            country,
            description,
            managerEmail,
            accountNumber,
            id,
            website
          )
        }
      >
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Name</label>
          </div>
          <div className="field-body">
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="name"
                  placeholder="Restaurant Name"
                  value={name}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-user" />
                </span>
              </p>
            </div>
          </div>
        </div>

        <div className="field is-horizontal ">
          <div className="field-label is-normal">
            <label className="label">Website</label>
          </div>
          <div className="field-body">
            <div className="field ">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="website"
                  placeholder="Website"
                  value={website}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i class="fas fa-link" />
                </span>
              </p>
            </div>
            <div className="field-label is-normal">
              <label className="label">Email</label>
            </div>
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="email"
                  name="email"
                  placeholder="Email"
                  value={email}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-envelope" />
                </span>
              </p>
            </div>
          </div>
        </div>
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Phone Number</label>
          </div>
          <div className="field-body">
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="number"
                  name="phoneNo"
                  placeholder="Phone No."
                  value={phoneNo}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-phone" />
                </span>
              </p>
            </div>
            <div className="field-label is-normal">
              <label className="label">Tax No.</label>
            </div>
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="number"
                  name="taxNo"
                  placeholder="Tax No."
                  value={taxNo}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-file-alt" />
                </span>
              </p>
            </div>
          </div>
        </div>

        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Address Line 1</label>
          </div>
          <div className="field-body">
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="line1"
                  placeholder="Address line 1"
                  value={line1}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-address-card" />
                </span>
              </p>
            </div>
            <div className="field-label is-normal">
              <label className="label">Line 2</label>
            </div>
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="line2"
                  placeholder="Address line 2"
                  value={line2}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-address-card" />
                </span>
              </p>
            </div>
          </div>
        </div>
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">City</label>
          </div>
          <div className="field-body">
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="city"
                  placeholder="City"
                  value={city}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-city" />
                </span>
              </p>
            </div>
            <div className="field-label is-normal">
              <label className="label">PIN</label>
            </div>
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="number"
                  name="pin"
                  placeholder="PIN"
                  value={pin}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-map-marker-alt" />
                </span>
              </p>
            </div>
          </div>
        </div>
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">State</label>
          </div>
          <div className="field-body">
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="state"
                  placeholder="State"
                  value={state}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-city" />
                </span>
              </p>
            </div>
            <div className="field-label is-normal">
              <label className="label">Country</label>
            </div>
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="country"
                  placeholder="Country"
                  value={country}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i className="fas fa-globe-asia" />
                </span>
              </p>
            </div>
          </div>
        </div>
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            {editing ? '' : <label className="label">Manager Email</label>}
          </div>
          <div className="field-body">
            {editing ? (
              ''
            ) : (
              <div className="field">
                <p className="control is-expanded has-icons-left">
                  <input
                    required
                    className="input"
                    type="email"
                    name="managerEmail"
                    placeholder="Email"
                    value={managerEmail}
                    onChange={this.handleChange}
                  />
                  <span className="icon is-small is-left">
                    <i className="fas fa-envelope" />
                  </span>
                </p>
              </div>
            )}
            <div className="field-label is-normal">
              <label className="label">Account Number</label>
            </div>
            <div className="field">
              <p className="control is-expanded has-icons-left">
                <input
                  required
                  className="input"
                  type="text"
                  name="accountNumber"
                  placeholder="Account Number"
                  value={accountNumber}
                  onChange={this.handleChange}
                />
                <span className="icon is-small is-left">
                  <i class="fas fa-piggy-bank" />
                </span>
              </p>
            </div>
          </div>
        </div>
        <div className="field is-horizontal">
          <div className="field-label is-normal">
            <label className="label">Description</label>
          </div>
          <div className="field-body">
            <div className="field">
              <div className="control">
                <textarea
                  className="textarea"
                  name="description"
                  value={description}
                  onChange={this.handleChange}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="field is-horizontal">
          <div className="field-label" />
          <div className="field-body">
            <div className="field">
              <div className="control">
                {editing ? (
                  <button className="button is-primary">Save Restaurant</button>
                ) : (
                  <button className="button is-primary">Add Restaurant</button>
                )}
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

export default RestaurantForm;
