import React, { Component, Fragment } from 'react';
import Notification from 'react-bulma-notification';
import RestaurantForm from '../restaurantForm/RestaurantForm';
import '../../../node_modules/react-bulma-notification/build/css/index.css';
import endpoint from '../../helpers/Endpoint';

class AddRestaurant extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      phoneNo: '',
      taxNo: '',
      line1: '',
      line2: '',
      city: '',
      pin: '',
      state: '',
      country: '',
      description: '',
      managerEmail: '',
      style: '',
      id: '',
      accountNumber: '',
      website: ''
      // name: 'adasd',
      // email: 'sadsad@sadsa.com',
      // phoneNo: '3',
      // taxNo: '3',
      // line1: '3',
      // line2: '4',
      // city: 'd',
      // pin: '3',
      // state: 'df',
      // country: 'dfd',
      // description: 'dfd',
      // managerEmail: 'tomayanknauriyal@gmail.com',
      // style: '',
      // id: '',
      // accountNumber: '343',
    };
  }

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  openNotification = (type, msg) => {
    Notification[type](<strong>{msg}</strong>, { duration: 5 });
  };

  addRestaurant = (
    e,
    editing,
    name,
    email,
    phoneNo,
    taxNo,
    line1,
    line2,
    city,
    pin,
    state,
    country,
    description,
    managerEmail,
    accountNumber,
    website
  ) => {
    e.preventDefault();
    if (!editing) {
      const token = localStorage.getItem('token');
      const data = JSON.stringify({
        name,
        phoneNo,
        email,
        taxNo,
        line1,
        line2,
        city,
        pin,
        state,
        country,
        description,
        managerEmail,
        accountNumber,
        website
      });
      function handleErrors(result) {
        if (!result.ok) {
          throw Error(result.status);
        }
        return result;
      }
      fetch(`${endpoint}/admin/addrestaurant`, {
        method: 'POST',
        body: data,
        headers: {
          'Content-Type': 'application/json',
          'x-auth-token': token
        }
      })
        .then(handleErrors)
        .then(result => result.json())
        .then(result => {
          if (result.status === 'failure') {
            console.log('errors');
            this.openNotification('error', result.data);
          } else {
            this.setState({ style: 'is-active', id: result.data._id });
            this.openNotification('success', result.message);
          }
        })
        .catch(error => console.log(error));
    }
  };

  handleIconChange = e => {
    const { id } = this.state;
    const file = e.target.files[0];
    const formData = new FormData();
    formData.append('logo', file);
    const token = localStorage.getItem('token');
    function handleErrors(result) {
      if (!result.ok) {
        throw Error(result.status);
      }
      return result;
    }
    fetch(`${endpoint}/admin/restaurant/${id}/logo`, {
      method: 'POST',
      body: formData,
      headers: {
        'x-auth-token': token
      }
    })
      .then(handleErrors)
      .then(result => result.json())
      .then(result => {
        if (result.status === 'failure') {
          console.log(result);
          this.openNotification('error', result.data);
        } else {
          this.setState({ style: '' });
          this.props.history.push('/');
          this.openNotification('success', result.message);
        }
      })
      .catch(error => console.log(error));
  };

  closeModal = () => {
    this.setState({ style: '' });
  };

  render() {
    const { style } = this.state;
    return (
      <Fragment>
        <RestaurantForm
          editing={false}
          saveRestaurant={(
            e,
            editing,
            name,
            email,
            phoneNo,
            taxNo,
            line1,
            line2,
            city,
            pin,
            state,
            country,
            description,
            managerEmail,
            accountNumber,
            website
          ) =>
            this.addRestaurant(
              e,
              editing,
              name,
              email,
              phoneNo,
              taxNo,
              line1,
              line2,
              city,
              pin,
              state,
              country,
              description,
              managerEmail,
              accountNumber,
              website
            )
          }
        />
        <div className={`modal ${style}`}>
          <div className="modal-background" />
          <div className="modal-content" style={{ zIndex: 1 }}>
            <div className="tile is-parent">
              <article className="tile is-child notification ">
                <div className="content">
                  <div className="file">
                    <label className="file-label">
                      <input
                        style={{ color: '#333' }}
                        className="file-input"
                        type="file"
                        name="icon"
                        onChange={this.handleIconChange}
                      />
                      <span className="file-cta">
                        <span className="file-icon">
                          <i className="fas fa-upload" />
                        </span>
                        <span className="file-label">Choose a file…</span>
                      </span>
                    </label>
                  </div>
                </div>
              </article>
            </div>
          </div>
          <div
            onClick={this.closeModal}
            style={{
              minWidth: '100%',
              minHeight: '100%',
              position: 'absolute'
            }}
          >
            <button className="modal-close is-large" aria-label="close" />
          </div>
        </div>
      </Fragment>
    );
  }
}

export default AddRestaurant;
