import React, { Component, Fragment } from 'react';
import RestaurantForm from '../restaurantForm/RestaurantForm';
import endpoint from '../../helpers/Endpoint';

class Restaurant extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editing: false,
      restaurant: []
    };
  }

  componentDidMount = () => {
    this.setState({ restaurant: this.props.restaurant });
  };

  handleRestaurantEdit = () => {
    this.setState(prevState => ({
      editing: !prevState.editing
    }));
  };

  saveRestaurant = (
    e,
    editing,
    name,
    email,
    phoneNo,
    taxNo,
    line1,
    line2,
    city,
    pin,
    state,
    country,
    description,
    managerEmail,
    accountNumber,
    id,
    website
  ) => {
    e.preventDefault();
    const data = JSON.stringify({
      name,
      email,
      phoneNo,
      taxNo,
      line1,
      line2,
      city,
      pin,
      state,
      country,
      description,
      accountNumber,
      website
    });
    const token = localStorage.getItem('token');
    function handleErrors(result) {
      if (!result.ok) {
        throw Error(result.status);
      }
      return result;
    }
    fetch(`${endpoint}/admin/restaurant/${id}`, {
      method: 'PUT',
      body: data,
      headers: {
        'Content-Type': 'application/json',
        'x-auth-token': token
      }
    })
      .then(handleErrors)
      .then(result => result.json())
      .then(result => {
        if (result.status === 'failure') {
          console.log('errors');
          this.openNotification('error', result.data);
        } else {
          console.log(result);
          this.openNotification('success', result.message);
        }
      })
      .catch(error => console.log(error));
  };

  render() {
    const { restaurant } = this.props;
    const { editing } = this.state;
    if (!editing) {
      return (
        <Fragment>
          <div className="columns is-centered">
            <div className="column">
              <div className="card">
                <header className="card-header">
                  <p className="card-header-title">{restaurant.name}</p>
                  <a
                    onClick={this.handleRestaurantEdit}
                    className="card-header-icon"
                    aria-label="more options"
                  >
                    Edit
                  </a>
                </header>
                <div className="card-content">
                  <div className="content">
                    <div className="columns">
                      <div className="column">
                        <img
                          src={`${endpoint}/${restaurant.logo}`}
                          style={{ maxWidth: 160, maxHeight: 160 }}
                        />
                      </div>
                      <div className="column">
                        <p>{restaurant.description}</p>
                        <p>{restaurant.website}</p>
                      </div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Restaurant Email
                      </div>
                      <div className="column">{restaurant.email}</div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Phone No.
                      </div>
                      <div className="column">{restaurant.phoneNo}</div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Tax No.
                      </div>
                      <div className="column">{restaurant.taxNo}</div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Account No.
                      </div>
                      <div className="column">{restaurant.accountNumber}</div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Address
                      </div>
                      <div className="column">
                        <p>
                          {restaurant.address.line1}, {restaurant.address.line2}
                        </p>
                        <p>
                          {restaurant.address.city}, {restaurant.address.pin}
                        </p>
                        <p>
                          {restaurant.address.state},{' '}
                          {restaurant.address.country}
                        </p>
                      </div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Manager Name
                      </div>
                      <div className="column">
                        {restaurant.employeeId.firstName}{' '}
                        {restaurant.employeeId.lastName}
                      </div>
                    </div>
                    <div className="columns">
                      <div className="column has-text-weight-semibold">
                        Manager Email
                      </div>
                      <div className="column">
                        {restaurant.employeeId.email}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Fragment>
      );
    } else if (editing) {
      return (
        <RestaurantForm
          editing={true}
          restaurant={restaurant}
          saveRestaurant={(
            e,
            editing,
            name,
            email,
            phoneNo,
            taxNo,
            line1,
            line2,
            city,
            pin,
            state,
            country,
            description,
            managerEmail,
            accountNumber,
            id,
            website
          ) =>
            this.saveRestaurant(
              e,
              editing,
              name,
              email,
              phoneNo,
              taxNo,
              line1,
              line2,
              city,
              pin,
              state,
              country,
              description,
              managerEmail,
              accountNumber,
              id,
              website
            )
          }
        />
      );
    }
  }
}

export default Restaurant;
